    nav.nav-container ul li {
      position: relative;
      background: red; }
    nav.nav-container ul ul {
      z-index: 100;
      position: absolute;
      visibility: hidden;
      opacity: 0;
      transition: visibility 0s linear 0.5s, opacity 0.5s linear; }
  nav.nav-container a {
    text-align: center;
